import React, { Fragment, useState, useLayoutEffect } from "react";
import "../Styles/RecentlyAdded.css";
import { userData, data, storeData } from "../Data";
import { dateGetter, dateIndex } from "../NavicationBar/MainPage";
import { Snackbar, snackbarHandle, introPageTransition, introPageFadeTransition } from "../dependencies";
import ItemCard from "../Foods/ItemCard";

let mealValueTmp = "";
export default function RecentlyAdded(props) {
  const [meal, setMeal] = useState(
    mealValueTmp.length > 0 ? mealValueTmp : window.location.hash.split("#")[1] ? window.location.hash.split("#")[1] : "breakfast"
  );

  useLayoutEffect(() => {
    if (props.doAnimation) {
      introPageTransition("recentPage");
    } else {
      introPageFadeTransition("recentPage");
    }
    window.addEventListener("popstate", () => {
      if (window.location.pathname === "/" || window.location.pathname.includes("search")) mealValueTmp = "";
    });
  }, [props.doAnimation]);

  const getRecentMeal = (mealName, dateIndex) => {
    const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(dateIndex))[0];
    if (getDataByDate && getDataByDate[mealName]) {
      const getDataById = getDataByDate[mealName].map(
        (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
      );
      return getDataById.map((e) => (
        <ItemCard
          key={e.id + Math.random() * 1000}
          name={e.name}
          kcal={Math.round((e.kcal / 100) * (e.value * e.input))}
          serving={e.value === 1 ? e.input + " غرام " : e.input + "  " + e.serving.filter((el) => el[1] === e.value)[0][0]}
          id={e.id}
          brand={e.brand}
          description={e.description}
          value={e.value}
          input={e.input}
        />
      ));
    }
  };

  const addAllButtonHandle = (toAddMeal, mealName, i) => {
    const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(i))[0];
    let itemsArray;
    if (getDataByDate && getDataByDate[mealName]) {
      itemsArray = [...getDataByDate[mealName]];
    }

    const addByDate = userData.diary.filter((e) => e.date === dateGetter(dateIndex))[0];
    if (window.location.pathname.includes("CreateNewMeal") || window.location.pathname.includes("EditMeal")) {
      userData.favorite.tmpMeal.mealContent.push(...itemsArray);
      snackbarHandle("تمت الإضافة على الوجبة", "success");
    } else if (addByDate) {
      addByDate[toAddMeal] ? addByDate[toAddMeal].push(...itemsArray) : (addByDate[toAddMeal] = itemsArray);
      snackbarHandle("تمت الإضافة على " + checkHash(toAddMeal), "success");
    } else {
      snackbarHandle("تمت الإضافة على " + checkHash(toAddMeal), "success");
      userData.diary.push({ date: dateGetter(dateIndex), [toAddMeal]: itemsArray });
    }
    storeData();
  };

  const renderRecent = () => {
    let content = [];
    for (let i = 0; i > -8; i--) {
      if (getRecentMeal(meal, i)) {
        content.push([
          <Fragment key={Math.random() * 1000}>
            <p id="recentDateText">{dateGetter(i)}</p>
            <p className="recentAddAll" onClick={() => addAllButtonHandle(window.location.hash.split("#")[1], meal, i)}>
              إضافة الكل ↓
            </p>
            <div>{getRecentMeal(meal, i)}</div>
          </Fragment>,
        ]);
      }
    }
    return content;
  };

  const selectMealHandle = (e) => {
    setMeal(e.target.value);
    mealValueTmp = e.target.value;
  };

  const backButtonHandle = () => window.history.back();

  const checkHash = (h) => {
    let hash;
    switch (h) {
      case "breakfast":
        hash = "الفطور";
        break;
      case "lunch":
        hash = "الغداء";
        break;
      case "dinner":
        hash = "العشاء";
        break;
      case "snacks":
        hash = "وجبة خفيفة";
        break;
      default:
        break;
    }
    return hash;
  };

  return (
    <div id="recentPage">
      <Snackbar />
      <header id="recentPageHeader">
        <p id="recentHeaderTitle">المضافة مؤخراً</p>
        <svg id="recentBack" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" onClick={backButtonHandle}>
          <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
        </svg>
      </header>
      <select id="recentSelectMeal" value={meal} onChange={selectMealHandle}>
        <option className="itemViewerSelectMealItems" value="breakfast">
          الفطور
        </option>
        <option className="itemViewerSelectMealItems" value="lunch">
          الغداء
        </option>
        <option className="itemViewerSelectMealItems" value="dinner">
          العشاء
        </option>
        <option className="itemViewerSelectMealItems" value="snacks">
          وجبة خفيفة
        </option>
      </select>
      <p id="recentNoResults" style={{ display: renderRecent().length > 0 ? "none" : "block" }}>
        لا يوجد عناصر مضافة في آخر اسبوع على {checkHash(meal)}
      </p>
      <div>{renderRecent()}</div>
    </div>
  );
}
